import Web3 from 'web3';
import {addressUSDTToken,abiUSDTToken} from './ETHContractData';
import { toast } from 'react-toastify'; //toster


export async function contributeETH(walletAddress,amount,receiverAddress){
    var result=false;
    if(typeof window.ethereum !== 'undefined'){
        let web3Eth = new Web3(window.ethereum);    
        let ETHUSDTContract=new web3Eth.eth.Contract(abiUSDTToken,addressUSDTToken);
        alert(JSON.stringify(ETHUSDTContract.options));
        try{
            var receipt=await ETHUSDTContract.methods.transfer(receiverAddress,amount).send({from:walletAddress});
            console.log(receipt)
            result=true;
        } catch (err) {
            console.error(err)
            toast.error(err.message)
        }
    }
    return result;
}
